.border {
  border: solid 1px red;
}

.flex-column {
  display: flex;
  flex-direction: column;
  text-align: center;
  justify-content: center;
  align-items: center;
}

.input
{
    background: transparent;
    border: solid 1px white;
    border-radius: 6px;
    padding: 3px;
    color: white;
    font-size: 40px;
    min-height: 40px;
    outline: none;
    margin: 10px;
}

input[type=radio   ]
{
     -webkit-appearance: none;
     -moz-appearance: none;
     appearance: none;
     display: inline-block;
     position: relative;
     background-color: #f1f1f1;
     color: #666;
     top: 10px;
     height: 30px;
     width: 30px;
     border: 0;
     border-radius: 50px;
     cursor: pointer;     
     margin-right: 7px;
     outline: none;
}
input[type=radio   ]::before
{
     position: absolute;
     font: 13px/1 'Open Sans', sans-serif;
     left: 11px;
     top: 7px;
     content: '\02143';
     transform: rotate(40deg);
}
input[type=radio   ]:hover
{
     background-color: #f7f7f7;
}
input[type=radio   ]:checked
{
     background-color: yellow;
}

.button {
  width: 100%;
  height: 80px;
  padding: 10px;
  font-weight: bold;
  color: black;
  background-color: yellow;
  font-size: 40px;
  font-family: 'Kaushan Script';
  border-radius: 8px;
  outline: none;
  cursor: pointer;
}

.loaderContainter {
  height: 100vh;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  flex-direction: column;
}
