@import "../styles.scss";

.background {
  width: 100%;
  height: auto;
  position: relative;
  &__img {
    width: 100vw;
    height: 100vh;
    object-fit: cover;
  }
  &__content {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    height: 100vh;
    background-color: rgba(0, 0, 0, 0.3);
    color: white;
  }
}
